import { Box, Grid, Paper, Typography, createTheme, useTheme, ThemeProvider } from '@mui/material';
import { useIntl } from 'react-intl';

// プロパティ
type Props = {
  description: string;
  image: string;
  imageText: string;
  title: string;
}

/// アイキャッチ
export function EyeCatch(props: Props) {
  // テーマの上書き
  const intl = useIntl();
  const theme = createTheme({
    ...useTheme(),
    typography: {
      fontSize: intl.locale === 'ja' ? 12 : 14,
      fontFamily: ['Noto Sans JP'].join(','),
    }
  });

  return (
    <ThemeProvider theme={theme}>
      <Paper
        sx={{
          position: 'relative',
          backgroundColor: 'grey.800',
          color: '#fff',
          mb: 4,
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center',
          backgroundImage: `url(${props.image})`,
        }}
      >
        {/* Increase the priority of the hero background image */}
        {<img style={{ display: 'none' }} src={props.image} alt={props.imageText} />}
        <Box
          sx={{
            position: 'absolute',
            top: 0,
            bottom: 0,
            right: 0,
            left: 0,
            backgroundColor: 'rgba(0,0,0,.3)',
          }}
        />
        <Grid container>
          <Grid item md={6}>
            <Box sx={{position: 'relative', p: { xs: 3, md: 6 }, pr: { md: 0 }}}>
              <Typography component="h1" variant="h3" color="inherit" gutterBottom>
                {props.title}
              </Typography>
              <Typography variant="h5" color="inherit" paragraph>
                {props.description}
              </Typography>
            </Box>
          </Grid>
        </Grid>
      </Paper>
    </ThemeProvider>
  );
}
