import { useContext, useEffect, useState } from 'react';
import { useIntl } from 'react-intl';
import { Container, Grid } from '@mui/material';

import { AppBase } from '../../pages';
import { AppCard, EyeCatch, Footer, Header, MarkdownContent, Sidebar } from '../../components';
import { AppStateContext } from '../../App';
import { useSearchParams } from 'react-router-dom';

// プロパティ
type Props = {
  eyeCatch?: {
    title: string;
    description: string;
    image: string,
    imageText: string,
  },
  cards?: {
    date: string;
    description: string;
    image: string;
    imageLabel: string;
    title: string;
    url: string;
  }[],
  postFileName: string,
}

// ページベース
export function PageBase({ eyeCatch, cards, postFileName } : Props) {
  // コンテンツを取得して表示する
  const fetchContent = async (fileName: string, lang: string) => {
    const file = await import(`../../assets/posts/${lang}/${fileName}`);
    const result = await (await fetch(file.default)).text();
    setContent(result);
  };

  // 表示言語が切り替わったらコンテンツを再取得して表示する
  const intl = useIntl();
  const [content, setContent] = useState<string>('');
  useEffect(() => {
    fetchContent(postFileName, intl.locale)
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [postFileName, intl]);
 
  // URLパラメータに言語の指定があれば言語を設定したうえ
  // コンテンツを再取得して表示する
  const { setLang } = useContext(AppStateContext);
  const [searchParams] = useSearchParams();
  useEffect(() => {
    if (searchParams.has('lang') && searchParams.get('lang') === 'ja') {
      setLang('ja');
      fetchContent(postFileName, 'ja');
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchParams]);

  return (
    <AppBase>
      <Container maxWidth="lg">
        <Header />
        <main>
          {eyeCatch && <EyeCatch { ...eyeCatch! } key={eyeCatch.title}/>}
          {cards &&
            <Grid container spacing={4}>
              {cards.map((post) => (<AppCard key={post.title} post={post} />))}
            </Grid>
          }
          <Grid container spacing={5}>
            <Grid item xs={12} md={8} sx={{ '& .markdown': { pt: 3 } }}>
              <MarkdownContent content={content} />
            </Grid>
            <Sidebar />
          </Grid>
        </main>
      </Container>
      <Footer />
    </AppBase>
  );
}
