import { useIntl } from 'react-intl';
import { Link as RouterLink } from 'react-router-dom';
import { Card, CardContent, CardActionArea, CardMedia, Grid, Typography, createTheme, useTheme, ThemeProvider } from '@mui/material';

import { EllipsisText } from './EllipsisText';

// プロパティ
interface Props {
  post: {
    title: string;
    date: string;
    description: string;
    image: string;
    imageLabel: string;
    url: string;
  };
}

// 記事カード
export function AppCard(props: Props) {
  // テーマの上書き
  const intl = useIntl();
  const theme = createTheme({
    ...useTheme(),
    typography: {
      fontSize: intl.locale === 'ja' ? 12 : 14,
      fontFamily: ['Noto Sans JP'].join(','),
    }
  });

  const { post } = props;

  return (
    <Grid item xs={12} md={6}>
      <CardActionArea component={RouterLink} to={post.url}>
        <Card sx={{ display: 'flex' }}>
          <CardContent sx={{ flex: 1 }}>
            <ThemeProvider theme={theme}>
              <EllipsisText text={post.title} lines={1} component="h2" variant="h5" />
            </ThemeProvider>
            <Typography variant="subtitle1" color="text.secondary">
              {post.date}
            </Typography>
            <ThemeProvider theme={theme}>
              <EllipsisText text={post.description} lines={2} variant="subtitle1" paragraph />
              <Typography variant="subtitle1" color="primary">
                {intl.formatMessage({ id: 'continueReading' })}
              </Typography>
            </ThemeProvider>
          </CardContent>
          <CardMedia
            component="img"
            sx={{width: 160, display: {xs: 'none', sm: 'block' }}}
            image={post.image}
            alt={post.imageLabel}
          />
        </Card>
      </CardActionArea>
    </Grid>
  );
}
