import { useContext } from 'react';
import { useIntl } from 'react-intl';
import { Link as RouterLink } from 'react-router-dom';
import { Link, Toolbar, ToggleButtonGroup, ToggleButton, Typography } from '@mui/material';

import { AppStateContext } from '../App';

// ヘッダー
export function Header() {
  const { appState, setLang } = useContext(AppStateContext);
  const intl = useIntl();

  // セクションの定義
  const sections = [
    { title: intl.formatMessage({ id: 'home' }), url: `${process.env.PUBLIC_URL}/` },
    { title: intl.formatMessage({ id: 'privacyPolicy' }), url: `${process.env.PUBLIC_URL}/privacy-policy` },
  ];

  // 言語切替スイッチアクション
  const handleSwitchLang = (event: React.MouseEvent<HTMLElement>, lang: string) => {
    if (lang !== null) {
      setLang(lang);
    }
  };

  return (
    <>
      <Toolbar sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Typography component="h2" variant="h5" color="inherit" align="center" noWrap sx={{ flex: 1 }}>
          {appState.title}
        </Typography>
        <ToggleButtonGroup exclusive value={appState.lang} onChange={handleSwitchLang} size="small">
          <ToggleButton value="en">En</ToggleButton>
          <ToggleButton value="ja">Ja</ToggleButton>
        </ToggleButtonGroup>
      </Toolbar>
      <Toolbar component="nav" variant="dense" sx={{ justifyContent: 'space-evenly', overflowX: 'auto' }}>
        {sections.map((section) => (
          <Link
            component={RouterLink}
            to={section.url}
            color="inherit"
            noWrap
            key={section.title}
            variant="body2"
            sx={{ p: 1, flexShrink: 0 }}
          >
            {section.title}
          </Link>
        ))}
      </Toolbar>
    </>
  );
}
