import Typography from '@mui/material/Typography';

// 省略可能なテキスト
export function EllipsisText({ text, lines, ...props }: any) {
  return (
    <Typography
      { ...props }
      sx={{
        width: "100%",
        display: "-webkit-box",
        WebkitBoxOrient: "vertical",
        WebkitLineClamp: lines,
        overflow: "hidden",
      }}
    >
      {text}
    </Typography>
  );
}
