import { createContext, useState } from "react";
import { IntlProvider } from 'react-intl';
import { createBrowserRouter, RouterProvider } from "react-router-dom";

import Blog from './blog/Blog';
import { Error404, Home, PrivacyPolicy, UkiyoMap } from './pages';

import en from './i18n/en.json';
import ja from './i18n/ja.json';

// ルーティングの定義
export const router = createBrowserRouter([{
  path: process.env.PUBLIC_URL,
  children: [
    { path: '*', element: <Error404 /> },
    { path: '', element: <Home /> },
    { path: 'sample', element: <Blog /> },
    { path: 'privacy-policy', element: <PrivacyPolicy /> },
    { path: 'ukiyo-map', element: <UkiyoMap /> },
  ],
}]);

// アプリケーション初期状態
const initialAppState = {
  title: 'RECT-ANGLE lab.',
  mail: 'rectangle0123@gmail.com',
  lang: 'en',
};

// アプリケーション状態コンテキスト
export const AppStateContext = createContext({
  appState: initialAppState,
  setLang: (lang: string) => {},
});

// アプリケーション
export default function App() {
  const [appState, setAppState] = useState(initialAppState);
  const setLang = (lang: string) => {
    setAppState({ ...appState, lang: lang });
  };

  return (
    <AppStateContext.Provider value={{appState, setLang}}>
      <IntlProvider messages={appState.lang === 'ja' ? ja : en} locale={appState.lang} defaultLocale="en">
        <RouterProvider router={router} />
      </IntlProvider>
    </AppStateContext.Provider>
  );
}
